@use "../styles" as s;

.bodyText {
    @include s.bodyText;

    div {
        margin-bottom: 1em;
    }
}

.defaultBackground {
    background-color: black;
}

.background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: background-image 1s ease-in-out;
    background-size: cover;
    background-position: center;

    > * {
        filter: none;
    }
}

.signatureWrapper {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 15vh;
}

.glowingSignature {
    filter: invert(1);
    width: 90%;
}

[class*="footer"] {
    border-top: none;
}